<template>
  <div class="hello">
    <el-row v-for="sec in sections">
      <el-col span="8">
        <div>{{ sec.title }}</div>
        <el-form :model="sec" label-width="120px">
          <div v-for="field in sec.fields">
            <el-form-item :label="field.name" v-if="isText(field)">
              <el-input v-model="field.value"></el-input>
            </el-form-item>
            <el-form-item :label="field.name" v-if="isSwitch(field)">
              <el-switch v-model="field.value"></el-switch>
            </el-form-item>
            <el-form-item :label="field.name" v-if="isList(field)">
              <div v-for="v in field.value">
                <el-input v-model="v.value"></el-input>
                <el-button>-</el-button>
              </div>
              <el-button style="width:150px">+</el-button>
            </el-form-item>
          </div>
        <el-button type="primary">保存修改</el-button>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import bus from '@/utils/bus.js'

export default {
  name: 'Config',
  methods: {
    isText(field){
      if (field.type == "text"){
        return true
      }
      return false
    },
    isSwitch(field){
      if (field.type == "switch"){
        return true
      }
      return false
    },
    isList(field){
      if (field.type == "list"){
        return true
      }
      return false
    },
    getSections(){
      var _this = this
      bus.$axios.get('/cfg/list').then((response) => {
        _this.sections = response.data
      }).catch(function (error) {
        alert(error);
      });
    }
  },
  data () {
    return {
      /*
      sections: [
        {
          title: "cfg",
          fields: [
            {
              name: "addr",
              type: "text",
              value: "0.0.0.0:8086",
            },
            {
              name: "enable",
              type: "checkbox",
              value: false,
            },
            {
              name: "fields",
              type: "list",
              value: [
                {
                  type: "text",
                  value: "name"
                },
                {
                  type: "text",
                  value: "ci_type"
                }
              ],
            }
          ]
        }
      ]*/
      sections: this.getSections(),
    }
  }
}
</script>

<style>
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  background: #EEEEEE;
  border-radius: 4px;
}
.el-input {
  width: 150px;
}
</style>



